import './styles.css';
import Faculty from "../../components/Faculty";
import {back, winners, allFaculties} from "../../config";
import {HotKeys} from "react-hotkeys";
import {useEffect, useRef, useState} from "react";
import FacultyWinner from "../../components/FacultyWinner";
import useWindowSize from 'react-use/lib/useWindowSize'
import Confetti from 'react-confetti'
import axios from "axios";
const keyMap = {
    NEXT: "d"
};

let iterator = winners.values()
let winnersList = [...Array(winners.length).fill(undefined)]

function Main() {
    // useEffect(() => {
    //     axios.get(`${back}/api/faculty`)
    //         .then(res => {
    //             allFaculties = res.data;
    //         }).catch(error => {
    //         console.log(error)
    //     })
    //     axios.get(`${back}/api/winners`)
    //         .then(res => {
    //             winners = res.data;
    //         }).catch(error => {
    //         console.log(error)
    //     })
    // },[])



    const inputRef = useRef()
    const [currFac, setFac] = useState(1000);
    const [counter, setCounter] = useState(0);
    const [confetti, setConfetti] = useState(false);
    const { width, height } = useWindowSize()
    const handlers = {
        NEXT: () => {
            setConfetti(true)
            setCounter(counter + 1)
            const Fac = iterator.next().value
            setFac(Fac)
            if (Fac)
                winnersList[counter] = Fac


        }
    }

    useEffect(() => {
        inputRef.current.focus()
    })
    return (
        <HotKeys keyMap={keyMap} handlers={handlers} allowChanges={true} innerRef={inputRef}>
            {confetti &&
            <Confetti
                gravity={0.1}
                initialVelocityY={20}
                width={width}
                height={height}
                recycle={false}
                numberOfPieces={500}
                onConfettiComplete={() =>  setConfetti(false)}
            />}
            <div className="App" style={{
                backgroundImage: `url(${process.env.PUBLIC_URL + '/assets/background.png'})`
            }}>
                <div className="line">
                    <div className="container">
                        <div className="right">
                            <div className="video"></div>
                            <div className="FacultyList">
                                {
                                    Object.keys(allFaculties).map((faculty, index) =>
                                        <Faculty key={faculty} params={allFaculties[faculty]} id={faculty}
                                                 currFac={currFac} showOpacity={true}
                                        />
                                    )
                                }
                            </div>
                        </div>
                        <div className="left">
                            <p className="title"> Им придется петь еще раз</p>
                            <div  className="winnersList">
                                {
                                    winnersList.map((faculty, index) =>
                                        <FacultyWinner key={index} params={allFaculties[faculty]}/>
                                    )}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </HotKeys>
    )
}

export default Main;